@custom-media --wide-vp (width >= 1280px);
@custom-media --not-wide-vp (width <= 1279.9px);
@custom-media --middle-vp (768px <= width < 1279.9px);
@custom-media --small-vp (width <= 767.9px);
@custom-media --not-small-vp (width >= 768px);
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
:root {
  --accent-font: "Oswald", "Impact", sans-serif;
  --main-font: "Arial", "Helvetica", sans-serif;
}

:root {
  --white: #ffffff;
  --black: #000000;
  --black-light: #111111;
  --green: #68b738;
  --grey-dark-special: #222222;
  --grey-dark: #444444;
  --grey-special: #666666;
  --grey-basic_background: #f2f2f2;
  --grey_background: #eaeaea;
  --green_background: rgba(104, 183, 56, 0.85);
  --transparent: rgba(0, 0, 0, 0);
}

img {
  max-width: 100%;
  height: auto;
  margin: 0;
}

.page {
  height: 100%;
  font-family: var(--main-font);
}

.page__body {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  background-color: var(--white);
}

.page__hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page__wrapper_center {
  margin: 0 20px;
}
@media (--middle-vp) {
  .page__wrapper_center {
    margin: 0 30px;
  }
}
@media (--wide-vp) {
  .page__wrapper_center {
    width: 1220px;
    margin: 0 auto;
  }
}

.page-main {
  flex-grow: 1;
}

.button {
  font-family: var(--accent-font);
  font-weight: 400;
  width: fit-content;
  text-decoration: none;
  text-transform: uppercase;
  color: var(--white);
  background-color: var(--green);
  border: none;
}
.button--size_wide {
  width: 100%;
}

.page-header {
  display: flex;
  flex-direction: column;
}
@media (--not-small-vp) {
  .page-header {
    display: grid;
    grid-template-columns: 1fr 2fr;
    padding: 15px 0;
  }
}
@media (--wide-vp) {
  .page-header {
    grid-template-columns: 1fr 1fr;
    padding: 40px 0;
  }
}

.page-header__wrapper-logo {
  display: flex;
  justify-content: center;
  padding: 10px 0;
}

.page-header__logo-link {
  text-decoration: none;
}

.page-footer {
  background-color: var(--grey-basic_background);
}

.page-footer__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (--not-small-vp) {
  .page-footer__container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.page-footer__iframe {
  border: none;
  width: 100%;
  height: 362px;
}
@media (--not-small-vp) {
  .page-footer__iframe {
    height: 400px;
  }
}

.page-footer__copyright {
  font-size: 16px;
  line-height: 18px;
}

.page-footer__wrapper_grid {
  background-color: var(--white);
}
@media (--wide-vp) {
  .page-footer__wrapper_grid {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto;
  }
}

@media (--wide-vp) {
  .page-footer__contacts {
    margin-top: 98px;
    grid-row: 1/-1;
    grid-column: 1/-1;
    z-index: 2;
  }
}

@media (--wide-vp) {
  .page-footer__location {
    grid-row: 1/-1;
    grid-column: 1/-1;
  }
}

.contacts {
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 1fr 1fr;
  background-color: var(--white);
  padding: 26px 20px;
}
@media (--middle-vp) {
  .contacts {
    padding: 56px 70px;
  }
}
@media (--wide-vp) {
  .contacts {
    width: 565px;
    height: 198px;
    padding: 56px 80px;
  }
}

.contacts__cooperation {
  font-style: normal;
  font-family: var(--accent-font);
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  color: var(--black-light);
  text-transform: uppercase;
}

.contacts__text {
  font-style: normal;
  font-size: 14px;
  line-height: 20px;
  color: var(--grey-dark);
}

.location__noscript-image {
  display: block;
  object-fit: cover;
  object-position: center center;
  width: 100%;
  height: 362px;
}
@media (--middle-vp) {
  .location__noscript-image {
    height: 400px;
  }
}
@media (--wide-vp) {
  .location__noscript-image {
    max-width: 1440px;
    height: 400px;
    margin: 0 auto;
  }
}

.social__list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.social__link {
  padding: 10px;
}

.copyright__link {
  color: var(--grey-dark);
  text-decoration: none;
}

.main-nav {
  font-family: var(--accent-font);
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
}

.main-nav__list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style-type: none;
  text-align: center;
}
@media (--not-small-vp) {
  .main-nav__list {
    flex-direction: row;
    justify-content: space-between;
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }
}
@media (--wide-vp) {
  .main-nav__list {
    font-size: 20px;
    line-height: 30px;
  }
}

.main-nav__item {
  padding: 10px 0;
}
@media (--not-small-vp) {
  .main-nav__item {
    padding: 10px;
  }
}

.main-nav__link {
  color: var(--black);
  text-decoration: none;
  text-transform: uppercase;
}
@media (--wide-vp) {
  .main-nav__link {
    color: var(--white);
  }
}

.selection {
  display: flex;
  flex-direction: column;
  font-family: var(--accent-font);
  color: var(--white);
  text-align: center;
}
@media (--small-vp) {
  .selection {
    position: relative;
  }
}
.selection::before {
  position: absolute;
  content: "";
  background-image: linear-gradient(to bottom, var(--green_background), var(--green_background)), url("../img/index-background-cat.jpg");
  width: 100%;
  height: 302px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
  z-index: -1;
}
@media (--middle-vp) {
  .selection::before {
    content: initial;
  }
}
@media (--wide-vp) {
  .selection::before {
    right: 0;
    top: 0;
    width: 50%;
    height: 100%;
    background-size: contain;
    background-position: center left;
  }
}

@media (--not-small-vp) {
  .selection__wrapper {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto;
  }
}
@media (--wide-vp) {
  .selection__wrapper {
    grid-template-columns: 1fr 1fr;
  }
}

.selection__title {
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  padding-top: 25px;
  margin-top: 0;
  margin-bottom: 25px;
}
@media (--not-small-vp) {
  .selection__title {
    font-size: 60px;
    line-height: 60px;
    color: var(--black);
    grid-column: 1/2;
    grid-row: 1/-1;
    margin-left: 87px;
  }
}
@media (--wide-vp) {
  .selection__title {
    margin: 0;
  }
}

.selection__slogan {
  font-style: normal;
  font-size: 14px;
  line-height: 14px;
  font-weight: 400;
  text-transform: uppercase;
  margin-top: 0;
}
@media (--not-small-vp) {
  .selection__slogan {
    font-size: 20px;
    line-height: 20px;
    color: var(--black);
    grid-column: 1/2;
    grid-row: 1/-1;
    margin-top: 180px;
    margin-left: 87px;
  }
}
@media (--wide-vp) {
  .selection__slogan {
    margin-top: 230px;
    margin-left: 80px;
  }
}

.selection__image {
  display: block;
  padding-top: 25px;
  margin: 0 auto;
}
@media (--not-small-vp) {
  .selection__image {
    width: 709px;
    height: 609px;
    grid-column: 1/2;
    grid-row: 1/-1;
    z-index: -1;
    margin-top: 220px;
    justify-self: center;
  }
}
@media (--wide-vp) {
  .selection__image {
    width: 552px;
    height: 499px;
    grid-column: 2/-1;
  }
}

.selection__button-link {
  font-size: 14px;
  line-height: 18px;
}
@media (--not-small-vp) {
  .selection__button-link {
    box-sizing: border-box;
    font-size: 20px;
    line-height: 20px;
    width: 244px;
    height: 54px;
    padding: 14px 26px;
    grid-column: 1/-1;
    grid-row: 1/-1;
    margin-top: 250px;
    margin-left: 87px;
  }
}
@media (--wide-vp) {
  .selection__button-link {
    margin-top: 300px;
    margin-left: 80px;
  }
}

.advantages {
  color: var(--grey-dark);
  font-size: 14px;
  line-height: 18px;
}
@media (--not-small-vp) {
  .advantages {
    font-size: 16px;
    line-height: 24px;
    color: var(--black);
  }
}

.advantages__list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (--wide-vp) {
  .advantages__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

.advantages__item {
  background-color: var(--grey-basic_background);
  padding: 20px;
}
.advantages__item:not(:last-child) {
  margin-bottom: 20px;
}
@media (--wide-vp) {
  .advantages__item:not(:last-child) {
    margin-bottom: 0;
    margin-right: 20px;
  }
}

.advantages__title {
  font-family: var(--accent-font);
  font-size: 24px;
  line-height: 37px;
  font-weight: 400;
  color: var(--black);
  text-transform: uppercase;
  margin: 0;
}
@media (--not-small-vp) {
  .advantages__title {
    font-size: 36px;
    line-height: 36px;
  }
}

.advantages__button-link {
  font-family: var(--accent-font);
  font-size: 16px;
  line-height: 16px;
  color: var(--black);
  text-decoration: none;
  text-transform: uppercase;
}
@media (--not-small-vp) {
  .advantages__button-link {
    font-size: 20px;
    line-height: 30px;
  }
}

.technologies {
  color: var(--grey-dark);
  font-size: 14px;
  line-height: 18px;
  padding: 20px 0;
}
@media (--not-small-vp) {
  .technologies {
    font-size: 16px;
    line-height: 24px;
  }
}

.technologies__title {
  font-family: var(--accent-font);
  font-size: 36px;
  line-height: 40px;
  font-weight: 400;
  color: var(--black);
  margin-top: 0;
  margin-bottom: 40px;
}
@media (--not-small-vp) {
  .technologies__title {
    font-size: 60px;
    line-height: 60px;
  }
}

.technologies__list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (--not-small-vp) {
  .technologies__list {
    flex-direction: row;
    flex-wrap: wrap;
  }
}

.technologies__item {
  margin-top: 0;
}
.technologies__item:not(:last-child) {
  margin-bottom: 20px;
}
@media (--not-small-vp) {
  .technologies__item {
    width: 50%;
    min-height: 280px;
  }
}
@media (--wide-vp) {
  .technologies__item {
    width: 25%;
  }
}

.compare {
  color: var(--grey-dark);
  font-size: 14px;
  line-height: 18px;
  padding: 20px 0;
  background-color: var(--grey_background);
}
@media (--not-small-vp) {
  .compare {
    font-size: 16px;
    line-height: 24px;
  }
}

@media (--wide-vp) {
  .compare__wrapper_grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

.compare__title {
  font-family: var(--accent-font);
  font-size: 36px;
  line-height: 40px;
  font-weight: 400;
  color: var(--black);
  padding-top: 5px;
  margin-top: 0;
  margin-bottom: 40px;
}
@media (--not-small-vp) {
  .compare__title {
    font-size: 60px;
    line-height: 60px;
  }
}
@media (--wide-vp) {
  .compare__title {
    grid-column: 1/2;
  }
}

@media (--wide-vp) {
  .compare__description {
    grid-column: 1/2;
  }
}

@media (--not-small-vp) {
  .compare__container-list {
    display: flex;
    justify-content: space-between;
  }
}
@media (--wide-vp) {
  .compare__container-list {
    flex-direction: column;
    justify-content: flex-start;
    grid-column: 1/2;
  }
}

.compare__effect-list {
  display: flex;
  justify-content: space-evenly;
}
@media (--wide-vp) {
  .compare__effect-list {
    justify-content: flex-start;
  }
}

.compare__effect-frame {
  display: flex;
  flex-direction: column;
}
@media (--wide-vp) {
  .compare__effect-frame {
    justify-content: flex-start;
  }
}

.compare__effect-term {
  font-size: 12px;
  line-height: 12px;
  text-transform: lowercase;
  order: 2;
}
@media (--not-small-vp) {
  .compare__effect-term {
    font-size: 14px;
    line-height: 14px;
  }
}

.compare__effect-definition {
  font-family: var(--accent-font);
  font-size: 24px;
  line-height: 24px;
  font-weight: 400;
  color: var(--black);
  text-transform: uppercase;
}
@media (--not-small-vp) {
  .compare__effect-definition {
    font-size: 30px;
    line-height: 37px;
  }
}

.compare__expenses-list {
  display: flex;
  font-family: var(--accent-font);
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
  color: var(--black);
  text-transform: uppercase;
  justify-content: center;
}
@media (--not-small-vp) {
  .compare__expenses-list {
    font-size: 20px;
    flex-direction: column;
    align-items: flex-end;
  }
}
@media (--wide-vp) {
  .compare__expenses-list {
    flex-direction: row;
    justify-content: flex-start;
  }
}

@media (--wide-vp) {
  .compare__range {
    grid-column: 2/-1;
  }
}

.compare__range-button {
  font-family: var(--accent-font);
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  color: var(--black);
  text-transform: uppercase;
  border: none;
  background-color: var(--transparent);
}

.compare__image {
  display: block;
  padding-top: 40px;
  margin: 0 auto;
}

.range__bar {
  display: flex;
  justify-content: center;
}

.form-selection {
  font-family: var(--accent-font);
  font-weight: 400;
}

.form-selection__title {
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  color: var(--black);
  padding-top: 27px;
  padding-left: 20px;
  margin-top: 0;
  margin-bottom: 37px;
}

.form-selection__description {
  font-size: 14px;
  line-height: 18px;
  color: var(--black);
  padding-left: 20px;
  margin-top: 0;
  margin-bottom: 20px;
  text-transform: uppercase;
}

.form-selection__attention {
  display: block;
  font-family: var(--main-font);
  font-size: 14px;
  line-height: 16px;
  font-weight: 400;
  color: var(--black);
  padding: 0 20px;
  margin-bottom: 32px;
}

.form-selection__set {
  margin: 0;
}

.form__button {
  margin: 20px;
}

.form-cat {
  font-size: 16px;
  line-height: 18px;
  color: var(--grey-dark);
  text-transform: uppercase;
  border: none;
  padding: 20px;
}

.form-cat__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.form-cat__item:not(:last-child) {
  margin-bottom: 14px;
}

.form-cat__field {
  font-family: var(--accent-font);
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: var(--grey-dark);
  text-transform: uppercase;
}

.form-radio {
  font-size: 16px;
  line-height: 18px;
  color: var(--grey-dark-special);
  text-transform: uppercase;
  border: none;
  padding: 20px;
}

.form-radio__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.form-radio__item:not(:last-child) {
  margin-bottom: 13px;
}

.form-owner {
  border: none;
  padding: 20px;
}

.form-owner__legend {
  font-size: 24px;
  line-height: 26px;
  color: var(--black);
  padding: 0;
}

.form-owner__list {
  font-size: 16px;
  line-height: 18px;
  color: var(--grey-dark);
  text-transform: uppercase;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.form-owner__item:not(:last-child) {
  margin-bottom: 14px;
}

.form-owner__field {
  font-family: var(--accent-font);
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: var(--grey-dark);
  text-transform: uppercase;
}

.form-commentary {
  padding: 20px;
}

.form-commentary__label {
  font-size: 24px;
  line-height: 26px;
  color: var(--black);
}

.form-commentary__field {
  font-family: var(--accent-font);
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: var(--grey-dark);
  text-transform: uppercase;
}

.form-checkbox {
  border: none;
  padding: 20px;
}

.form-checkbox__legend {
  font-size: 24px;
  line-height: 26px;
  color: var(--black);
  padding: 0;
}

.form-checkbox__list {
  font-size: 16px;
  line-height: 18px;
  color: var(--black);
  text-transform: uppercase;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.form-checkbox__item:not(:last-child) {
  margin-bottom: 12px;
}

.catalog-main__title {
  font-family: var(--accent-font);
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  color: var(--black);
  padding-top: 27px;
  padding-left: 20px;
  margin-top: 0;
  margin-bottom: 40px;
}

.catalog__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.catalog__card {
  padding: 20px;
}

.product__link {
  text-decoration: none;
}

.product__title {
  font-family: var(--accent-font);
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  color: var(--grey-dark-special);
  text-transform: uppercase;
  margin: 0;
}

.product__title--more {
  color: var(--black-light);
  margin-bottom: 8px;
}

.product__specification {
  font-size: 12px;
  line-height: 16px;
  font-weight: 400;
  color: var(--grey-dark);
  margin: 0;
}

.product__description--more {
  font-size: 12px;
  line-height: 16px;
  font-weight: 400;
  color: var(--grey-dark);
  margin-top: 0;
  margin-bottom: 21px;
}

.product__definition {
  margin: 0;
}

.product__button {
  font-size: 16px;
  line-height: 20px;
}

.product__button--more_color {
  color: var(--grey-dark);
  background-color: var(--grey-basic_background);
}

.catalog-add__card:not(:last-child) {
  margin-bottom: 19px;
}

.catalog-add__list {
  padding: 20px;
}

.catalog-add__title {
  font-family: var(--accent-font);
  font-size: 24px;
  line-height: 24px;
  font-weight: 400;
  color: var(--black);
  padding-top: 20px;
  padding-left: 20px;
  margin-top: 0;
  margin-bottom: 36px;
}

.catalog-add__aside {
  margin: 20px;
}

.product-add__title {
  font-family: var(--accent-font);
  font-size: 16px;
  line-height: 16px;
  font-weight: 400;
  text-transform: uppercase;
}

.product-add__link {
  color: var(--grey-dark-special);
  text-decoration: none;
}

.product-add__quantity,
.product-add__price {
  font-size: 14px;
  line-height: 14px;
  font-weight: 400;
  color: var(--grey-dark);
  margin: 0;
}

.product-add__price {
  text-transform: uppercase;
}

.product-add__order {
  font-family: var(--accent-font);
  font-size: 16px;
  line-height: 20px;
  margin-top: 12px;
  margin-left: 0;
}

.catalog-aside {
  font-size: 16px;
